* {
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: #0f1325;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-weight: 400;
}

canvas {
  display: block;
}

#container {
  width: 100vw;
  height: 100vh;
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
}

.content {
  flex-direction: row;
  gap: 100px;
  padding: 200px;
  display: inline-flex;
}

.item, .item a {
  color: #eee;
  text-decoration: none;
}

.item h2 {
  color: #fff;
  font-weight: 100;
}

.item p {
  color: #ccc;
  font-weight: 100;
}

.item:last-child {
  margin-right: 300px;
}

img {
  width: 300px;
  height: 300px;
  opacity: .1;
}

.logo {
  z-index: 10;
  color: #fff;
  text-shadow: #ccc;
  font-size: 32px;
  font-weight: 100;
  text-decoration: none;
  position: fixed;
  top: 50px;
  left: 50px;
}

.single h1 {
  margin-bottom: 1em;
  font-size: 3em;
}

figure {
  gap: 50px;
  margin: 0 0 3em;
  display: flex;
}

figure + figure {
  flex-direction: row-reverse;
}

figure img {
  float: left;
  margin-right: 30px;
}

.single__content {
  max-width: 1000px;
  margin: 50px auto;
}

.single__content p {
  margin: 2em 0;
  font-size: 1.1em;
  line-height: 1.7;
}

.single__content a {
  color: #fff;
}

.single__top {
  height: 100vh;
  width: 100%;
  background-position: 50%;
  background-size: cover;
}

.footer {
  border-top: 1px solid #ccc;
  margin-top: 100px;
  padding-top: 10px;
  padding-bottom: 150px;
  font-size: .8em;
  display: flex;
}

.footer p + p {
  text-align-last: left;
  margin-left: auto;
}

.curtain, .curtainBottom {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(0, 100%);
}

.curtainTop {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(0, -100%);
}

.curtainBottom {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(0, 100%);
}

[data-barba-namespace] {
  position: absolute;
  top: 0;
}

